<template>
  <div>
    <el-form
      ref="loginForm"
      :model="form"
      :rules="rules"
      label-width="80px"
      class="login-box"
    >
      <h3 class="login-title"></h3>
      <el-form-item label="" prop="username">
        <el-input
          type="text"
          placeholder=""
          v-model="form.username"
        />
      </el-form-item>
      <el-form-item label="" prop="tel">
        <el-input
          type="text"
          placeholder=""
          v-model="form.tel"
        />
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input
          type="password"
          placeholder=""
          v-model="form.password"
        />
      </el-form-item>
      <el-form-item label="" prop="checkPassword">
        <el-input
          type="password"
          placeholder=""
          v-model="form.checkPassword"
        />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" v-on:click="doSignin"
          ></el-button
        >
      </el-form-item>
    </el-form>

    <!-- <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false"> </el-button>
      </span>
    </el-dialog> -->
  </div>
</template>

<script>
export default {
  name: "Signin",
  data() {
    return {
      form: {
        username: "",
        password: "",
        checkPassword: "",
        tel:"",
      },

      //  el-form-item  prop 
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" },
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
        ],
        checkPassword: [
          { required: true, message: "", trigger: "blur" },
        ],
        tel: [
          { required: true, message: "", trigger: "blur" },
        ],
      },

      // 
      dialogVisible: false,
    };
  },
  methods: {
    doSignin() {
      if (this.form.password != this.form.checkPassword) {
        alert("!");
      } else {
        this.$http
          .request({
            method: "post",
            url: "/user/insertUser",
            data: {
              username: this.form.username,
              pass: this.form.password,
              tel: this.form.tel,
            },
          })
          .then((res) => {
              let {state} = res.data;
              if(state === 'success'){
                    this.$router.push('/');
              }else {
                  alert("");
              }
          });
      }
    },
    // onSubmit(formName) {
    //   // 
    //   this.$refs[formName].validate((valid) => {
    //     if (valid) {
    //       //  vue-router 
    //       this.$router.push("/main");
    //     } else {
    //       this.dialogVisible = true;
    //       return false;
    //     }
    //   });
    // }
  },
};
</script>

<style scoped>
.login-box {
  border: 1px solid #dcdfe6;
  width: 350px;
  margin: 180px auto;
  padding: 35px 35px 15px 35px;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  box-shadow: 0 0 25px #909399;
}

.login-title {
  text-align: center;
  margin: 0 auto 40px auto;
  color: #303133;
}
</style>